import { useContext } from "react";
import { useSelector, useDispatch } from "react-redux";
import {
  closeUnsubscribeForm,
  removeSubscriber,
} from "../../store/subscriberSlice";

import { TranslationContext } from "../../context/TranslationContext";

import { useFormWithValidation } from "../Hooks/useForm";
import PopupWithForm from "./PopupWhitForm/PopupWithForm";
import Input from "../Input/Input";
import Button from "../Button/Button";

const UnsubscribePopup = () => {
  const { subscribers, isUnsubscribeFormOpen, status, error } = useSelector(
    (state) => state.subscribers
  );
  const { values, errors, isValid, handleChange, resetForm } =
    useFormWithValidation();
  const translation = useContext(TranslationContext);

  const dispatch = useDispatch();

  const handleCloseUnsubscribeForm = (e) => {
    dispatch(closeUnsubscribeForm());
    resetForm();
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    dispatch(removeSubscriber(values));
    handleCloseUnsubscribeForm();
  };

  const submitButtonClassName = `button button__submit ${
    isValid ? "button__submit_valid" : "button__submit_invalid"
  }`;

  return (
    <PopupWithForm
      isOpen={isUnsubscribeFormOpen}
      onClose={handleCloseUnsubscribeForm}
      title={translation.popup_unsubscribe_title}
      onSubmit={handleSubmit}
    >
      <Input
        type="email"
        inputName="email"
        labelName={translation.main_form_labelemail}
        value={values.email || ""}
        onChange={handleChange}
        errors={errors.email}
      />
      <Button
        isValid={!isValid}
        type="submit"
        className={submitButtonClassName}
      >
        {translation.popup_unsubscribe}
      </Button>
    </PopupWithForm>
  );
};

export default UnsubscribePopup;
